<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">采购退货</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{ $route.query.id ? '修改' : '添加' }}采购退货单</el-breadcrumb-item>
    </el-breadcrumb>
    <div>
      <div class="search_container ">
        <el-form :inline="true" :model="params" ref="ruleForm" class="demo-form-inline">
          <el-form-item label="采购组织" size="mini" prop="company_id" :rules="rules.company_name">
            {{(function(){if(params.company_id == 0)params.company_id = null})()}}
            <el-select v-model="params.company_id" @focus="companyListInvok" @change="companyChange" clearable filterable :disabled="line === 1">
              <el-option v-for="item in companyList" :label="item.company_name" :value="item.company_id" :key="item.company_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="单据号" label-width="110px" size="mini">
            <el-input v-model="params.refund_sn" disabled></el-input>
          </el-form-item>
          <el-form-item label="来源单据" size="mini">
            <!-- 新增才能叉掉 -->
            <el-input v-model="params.instorage_sn" :class='{disabledInstorage_sn : editType === 2}' @click.native="quoteOrder" :clearable="editType == 1" @clear="clearF" :disabled="line===2 || editType === 2" style="cursor:pointer;width: 200px;" :readonly="isShowCustomBill" suffix-icon="el-icon-more"></el-input>
          </el-form-item>
          <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
            <el-date-picker v-model="params.business_time" type="date" placeholder="选择日期" value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="出库类型" size="mini">
            <el-input type="text" maxlength="300" value="采购退货" disabled="disabled"></el-input>
          </el-form-item>
          <el-form-item label="仓库名称" size="mini" prop="warehouse_id" :rules="rules.warehouse_id">
            <el-select v-model="params.warehouse_id" placeholder="请选择" filterable @focus="warehouseInvok" @change="warehouseChange">
              <el-option v-for="item in warehouseList" :key="item.warehouse_id" :label="item.warehouse_name" :value="item.warehouse_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="供应商(厂家)" size="mini" prop="supplier_id" :rules="rules.supplier_id">
            <el-select clearable v-model="params.supplier_id" maxlength="100" :filter-method="supplierListInvok" :disabled="line === 1" @focus="supplierListInvok" filterable placeholder="请选择">
              <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            {{(function(){if(params.department_id == 0)params.department_id = null})()}}
            <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @focus="DepartmentListInvok" @change="departmentChange">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="采购员" size="mini">
            {{(function(){if([0,1,2].includes(params.buyer_id) || params.company_admin_id > 0)params.buyer_id = null})()}}
            <el-select clearable v-model="params.buyer_id" placeholder="请选择" filterable @focus="EmployeeListInvok">
              <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="退货原因" size="mini">
            {{(function(){if(params.reason_id == 0)params.reason_id = null})()}}
            <el-select clearable v-model="params.reason_id" @focus="getReasonInvok">
              <el-option v-for="item in reasonList" :label="item.reason_name" :value="item.reason_id" :key="item.reason_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="备注" size="mini">
            <el-input type="text" v-model="params.remark" maxlength="50" placeholder="请输入"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF('ruleForm')" v-if="!onlyQuote" :disabled="line==1" size="mini">增行</el-button>
      <el-button plain icon="el-icon-delete" @click="deleteF" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <xTable ref='xTable' :isEdit='isEdit' :indexSignStart='1' :indexSign='indexSign' :edit-rules='editRules' :tableColumn='tableColumn' :showFooter='true' :totalArray="['goods_num','goods_price']" />
      <div class="refundauditFoot">
        <span>创建人：{{params.add_user_name}}</span>
        <span>创建时间：{{params.add_time}}</span>
        <span>审核人：</span>
        <span>审核时间：</span>
        <span>修改人：{{params.update_user_name}}</span>
        <span>修改时间：{{params.update_time}}</span>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="goBack">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </div>
    </div>

    <!--引用入库单-->
    <div v-if="isShowCustomBill">
      <select-bill :isShowCustomBill="isShowCustomBill" @selectOutstockResult="selectOutstockResult" :selectParams='params' :editType="editType"></select-bill>
    </div>
    <!--货位列表-->
  </div>
</template>
<style lang="less" scoped>
.refundauditFoot {
  text-align: right;
  margin-top: 20px;
  font-size: 13px;

  span {
    display: inline-block;
    margin-right: 20px;
  }
}
</style>

<script>
import selectBill from "./selectBill";
import { opthionList } from "common/utils/public_function/commonUtils";
import { tableHead, rowModule } from "./config/refundAdd.js";
export default {
  name: "purchaseRefundAdd",
  mixins: [tableHead],
  components: {
    selectBill
  },
  data() {
    return {
      editType: 1, // 1是新增 2是修改
      params: {},
      indexSign: [],
      onlyQuote: false,
      tableData: [],
      readonly: false,
      loading: false,
      isShowCustomBill: false, //来源单号
      line: null // 1 是引入单据、2是手动新增
    };
  },
  methods: {
    // 计算价格
    computedPrice({ row, column }) {
      var obj = {
        num: "goods_num", // 商品数量的key
        price: "purchase_price", // 商品价格的key
        goodsPrice: "goods_price", // 商品总金额的key  数量 * 单价
        taxrate: "taxrate", // 税率
        revenue_price: "revenue_price", // 税额
        no_revenue_price: "no_revenue_price" // 不纳税额
      };
      this.$publicFution.computeTotal(row, obj, column.property, this);
      this.$refs.xTable._proxy("updateFooter");
    },
    // 打开来源单据入口
    quoteOrder() {
      if (this.line != 2) {
        this.isShowCustomBill = true;
      }
    },
    // 修改部门
    departmentChange(val) {
      this.$set(this.params, "buyer_id");
    },
    companyChange(val) {
      this.$refs.xTable._proxy("loadData", []);
      this.$refs.xTable._proxy("updateFooter");
      this.$set(this.params, "warehouse_id", "");
      this.$set(this.params, "reason_id", 0);
      this.$set(this.params, "buyer_id");
      this.$set(this.params, "supplier_id", "");
      this.$set(this.params, "department_id", "");
      this.$baseData.settingInfo({ num: 3, company_id: val },()=>{
      let state = localStorage.getItem("FDBName_business");
      this.onlyQuote = eval(state)[7].status == 1 ? true : false;
      });

    },
    clearF() {
      this.params = {};
      this.$refs.xTable._proxy("loadData", []);
      this.$refs.xTable._proxy("updateFooter");
      this.line = null;
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date())
      });
    },
    // 增行
    addF() {
      if (this.params.warehouse_id && this.params.company_id) {
        this.line = 2;
        this.$refs.xTable._proxy(
          "insertAt",
          this.$publicFution.deepCopy(rowModule),
          -1
        );
        this.$refs.xTable._proxy("updateFooter");
      } else {
        this.$message.warning("请先选择仓库和组织");
      }
    },
    warehouseChange(val) {
      var box = this.warehouseList.filter(item => item.warehouse_id === val);
      this.params.warehouse_name = box[0] ? box[0].warehouse_name : "";
    },
    // 删行
    deleteF() {
      if (this.$refs.xTable._proxy("getSelectRecords").length) {
        this.$refs.xTable._proxy("removeSelecteds");
      } else {
        this.$message.warning("请先选择要删除的商品！");
      }
      if (this.$refs.xTable._proxy("getTableData").fullData.length === 0) {
        if (!this.params.instorage_sn) this.$set(this, "line", null);
      }
    },
    selectOutstockResult(obj) {
      if (obj) {
        this.employeeList = [
          {
            employee_id: obj.info.buyer_id,
            employee_name: obj.info.buyer_name
          }
        ];
        opthionList(this, obj.info);
        this.params = obj.info;
        let dataList = obj.goodsList.map(obj => {
          obj.can_refund_num = obj.remaining_goods_num;
          obj.goods_num = obj.goods_num - obj.reference_goods_num;
          obj.goods_price = obj.subtotal_price;
          this.computedPrice({ row: obj, column: { property: 'goods_price' } })
          if (obj.allocation_id === 0) {
            obj = "";
          }
          return obj;
        });
        this.line = 1;
        this.$refs.xTable._proxy("loadData", dataList);
        this.$refs.xTable._proxy("updateFooter");
        this.params.business_time = this.$publicFution.formatData(new Date());
        // this.params.instorage_id = value;
      }
    },
    goBack() {
      this.$publicFution.goPage(this.$route.query.id);
      this.$router.push({ path: "/purchase/refund" });
    },
    // 提交参数
    submitForm(ruleForm) {
      this.$refs[ruleForm].validate(valid => {
        if (valid) {
          if (this.params.instorage_sn == undefined && this.line != 2) {
            this.$message.error("请引用单据");
            return false;
          }
          var tableData = this.$refs.xTable
            ._proxy("getTableData")
            .fullData.concat([]);
          if (tableData.length < 1) {
            this.$message.warning("请完善表格数据");
          }
          if (!tableData[tableData.length - 1].goods_id) {
            tableData.pop();
          }
          this.$refs.xTable._proxy("validate", tableData).then(() => {
            for (let i = 0; i < tableData.length; i++) {
              tableData[i].serial_number = i + 1;
              tableData[i].line = this.line == 2 ? 2 : 1;
              tableData[i] = this.$publicFution.filter(rowModule, tableData[i]);
            }
            let obj = {
              line: this.line == 2 ? 2 : 1,
              goods_json: JSON.stringify(tableData)
            };
            var url = "addRefund";
            var newObj = Object.assign(obj, this.params);
            if (this.$route.query.id) {
              url = "editRefund";
              newObj.refund_id = this.$route.query.id;
            }
            // 校验对接车郎中
            this.$baseData
              .checkCarDoctor({
                company_id: this.params.company_id,
                company_name: this.params.company_name,
                bill_type: 14,
                bill_id: this.$route.query.id,
                operation_type: this.$route.query.id ? 2 : 1,
                goods_info: JSON.stringify(
                  Array(tableData.length).fill({
                    warehouse_id: this.params.warehouse_id,
                    warehouse_name: this.params.warehouse_id
                  })
                )
              })
              .then(() => {
                this.$baseData
                  .checkComputation(tableData, {
                    goods_num: "goods_num",
                    total_price: "goods_price",
                    goods_price: "purchase_price"
                  })
                  .then(() => {
                    this.$axios({
                      method: "post",
                      url: this.$api[url],
                      data: newObj
                    }).then(res => {
                      if (res.data.code == 200) {
                        this.$message.success(res.data.msg);
                        this.goBack();
                      } else {
                        this.indexSign = res.data.data;
                      }
                    });
                  });
              });
          });
        }
      });
    }
  },
  mounted() {
    let state = localStorage.getItem("FDBName_business");
    if (state && state != "undefined") {
      // let state = localStorage.getItem("FDBName_business");
      this.onlyQuote = eval(state)[7].status == 1 ? true : false;
    }
    this.editType = 1;
    if (this.$route.query && this.$route.query.id) {
      this.editType = 2;
      this.$axios({
        method: "get",
        url: this.$api.getRefundInfo,
        params: {
          refund_id: this.$route.query.id
        }
      }).then(({ data }) => {
        if (data.code == 200) {
          this.params = data.data.info;
          this.$refs.xTable._proxy("loadData", data.data.goods_list);
          this.$refs.xTable._proxy("updateFooter");
          this.params = data.data.info;
          this.$set(this, "line", data.data.info.line);
          Object.assign(data.data.info, {
            employee_id: data.data.info.buyer_id,
            employee_name: data.data.info.buyer_name
          });
          opthionList(this, data.data.info);
          this.params.reason_id =
            data.data.info.reason_id == 0 ? "" : data.data.info.reason_id;
        }
      });
    } else {
      let { employee_id, company_admin_id } = localStorage;
      if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
        this.$set(this, "params", {
          business_time: this.$publicFution.formatData(new Date()),
          company_id: Number(localStorage.company_id)
        });
      } else {
        this.$set(this, "params", {
          business_time: this.$publicFution.formatData(new Date()),
          company_id: Number(localStorage.company_id),
          department_id: Number(localStorage.department_id),
          buyer_id: Number(localStorage.employee_id)
        });
      }
      opthionList(this, localStorage);
    }
  }
};
</script>

<style lang="less"  scoped>
.disabledInstorage_sn {
  /deep/ .el-input__inner {
    background-color: white !important;
    cursor: pointer;
  }
}
</style>
