// 行模型
export const rowModule = {
  "serial_number": '',
  "goods_id": "",
  "goods_price": "",
  "instorage_goods_id": "",
  "purchase_price": "",
  "batch": "",
  "remark": "",
  "allocation_id": "",
  "goods_num": '',
  "can_refund_num": "",
  "production_time": "",
  "refund_goods_id": 0,
  "no_revenue_price": '',
  "taxrate": '',
  "revenue_price": '',
  "line": 1,// 1是引用采购订单 2是手动增行
}

export const tableHead = {// 需要传进来一个this对象
  data() {
    return {
      editRules: {
        goods_num: [
          { required: true, message: '数量输入请在0-99999999之间' }
        ],
        goods_name: [
          { required: true, message: '请选择商品' }
        ],
        allocation_name: [
          { required: true, message: '请选择货位' }
        ],
        purchase_price: [
          { required: true, message: '请输入采购价' }
        ],
      },
      tableColumn: [
        {
          type: "checkbox",
          width: 44,
          align: 'center'
        },
        {
          title: '序号',
          type: "seq",
          width: 50
        },
        {
          title: '操作',
          fixed: 'right',
          width: 80,
          field: 'goods_id',
          slots: {
            default: (scope, h) => {
              return [
                <i class="el-icon-document" onClick={() => { this.$refs.xTable._copyRow(scope.row) }} style="cursor: pointer;font-size: 15px;line-height: 28px;" ></i >
              ]
            }
          },
        },
        {
          title: '商品编号',
          field: 'goods_id',
          width: 80
        },
        {
          title: '商品名称',
          field: 'goods_name',
          width: 400,
          editRender: {
            name: 'vGoods',
            getParams: ({ row }) => {
              return {
                params: {
                  isTotal: 1,
                  company_id: this.params.company_id,
                  warehouse_id: this.params.warehouse_id,
                  is_incomplete: 0
                },
                getIcon: () => {
                  if (row.goods_name) {
                    return [{
                      icon: 'el-icon-close',
                      title: '删除商品',
                      iconMethod: () => {
                        for (var i in row) {
                          row[i] = ''
                        }
                      }
                    }]
                  } else {
                    return []
                  }
                }
              }
            },
            events: {
              change: (val, { row }) => {
                // 修改商品
                [
                  "allocation_id",
                  "allocation_name",
                  "batch_id",
                  "unit_name",
                  "factory_code",
                  "purchase_price",
                  "production_time",
                  "return_goodsNum",
                  "refund_goods_id",
                  "goods_price",
                ].forEach(key => {
                  if (key == 'refund_goods_id') {
                    row[key] = 0;
                  } else {
                    row[key] = null;
                  }
                });
                let goodsInfo = [
                  "goods_id",
                  "goods_sn",
                  "stocks",
                  "factory_code",
                  "purchase_price",
                  "unit_name",
                  "taxrate"
                ];
                goodsInfo.forEach(key => {
                  row[key] = val[key];
                });
                var result = this.$refs.xTable._proxy("getTableData").fullData
                if (result[result.length - 1].goods_name) {
                  this.addF()
                }
              },
            }
          }
        },
        {
          title: '年周/批次',
          field: 'batch',
          width: 120,
          editRender: {
            name: 'vBatch',
            getParams: ({ row }) => {
              return {
                url: 'getGoodsBatchList',
                params: {
                  company_id: this.params.company_id,
                  warehouse_id: this.params.warehouse_id,
                  goods_id: row.goods_id ? row.goods_id : ""
                }
              }
            },
            events: {
              change: (val, { row }) => {
                [
                  'batch',
                  "batch_id",
                  "production_time",
                  "allocation_id",
                  "allocation_name"
                ].forEach(key => {
                  row[key] = val[key]
                });
              }
            }
          },
        },
        {
          title: '生产日期',
          field: 'production_time',
          width: 100,
          slots: {
            header: (scope, h) => {
              return [
                <el-tooltip class="item" content="Bottom center" effect="light">
                  <p slot="content">生产日期列后期将会隐藏，同时新增商品入库日期列</p>
                  <span
                    class="el-icon-question"
                    style="cursor:pointer;font-weight:bold;"
                  >生产日期</span>
                </el-tooltip>
              ]
            }
          },
        },
        {
          title: '单位',
          field: 'unit_name',
          width: 100
        },
        {
          title: '货位名称',
          field: 'allocation_name',
          width: 130,
          editRender: {
            name: 'vAllocation',
            getParams: ({ row }) => {
              return {
                url: 'newAllocationListList',
                isPage: true,
                params: {
                  company_id: this.params.company_id ? this.params.company_id : "",
                  warehouse_id: this.params.warehouse_id ? this.params.warehouse_id : "",
                  goods_id: row.goods_id,
                  is_incomplete: 0,
                  batch: row.batch
                }
              }
            },
            events: {
              change: ({ row }, val) => {
                row.allocation_id = val.allocation_id
                row.allocation_name = val.allocation_name
              }
            }
          }
        },
        {
          title: '数量',
          field: 'goods_num',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          width: 110,
          editRender: {
            name: 'number',
            events: {
              input: this.computedPrice
            }
          }
        },
        {
          title: '采购价',
          field: 'purchase_price',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          width: 132,
          editRender: {
            name: 'Vinput',
            formatter: 'String',
            events: {
              input: this.computedPrice
            }
          }
        },
        {
          title: '小计金额',
          field: 'goods_price',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          width: 132,
          editRender: {
            name: 'Vinput',
            events: {
              input: this.computedPrice
            }
          }
        },
        {
          field: 'revenue_price',
          showOverflow: true,
          title: '税额',
          width: 100,
          editRender: {
            autoselect: true,
            name: 'vNumber',
            events: {
              input: this.computedPrice
            },
          }
        },
        {
          field: 'no_revenue_price',
          showOverflow: true,
          width: 100,
          title: '不含税金额'
        },
        {
          title: '工厂代码',
          field: 'factory_code',
          width: 150
        },
        {
          title: '备注',
          field: 'remark',
          width: 200,
          editRender: {
            name: 'Vinput',
            formatter: 'String'
          }
        }
      ]
    }
  },
  methods: {// 
    isEdit({ row, column }) {
      if (column.property === 'goods_name' || column.property === 'batch') {
        if (this.line === 1) {
          this.$publicFution.confirm('引用单据不可修改')
          return false
        }
      }
      if (column.property === 'batch' && !row.goods_name) {
        this.$publicFution.confirm('请先选择商品')
        return false
      }
      return true
    }
  }
}