<template>
  <div>
    <el-dialog :close-on-click-modal="false" title="引用采购入库单据" :visible.sync="$parent.isShowCustomBill" width="1280px" @close="cancel()">
      <el-form :inline="true" ref="params" :model="params">

        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="单据号" clearable>
          <el-input v-model="params.instorage_sn" placeholder="请输入" :disabled="editType === 2" clearable></el-input>
        </el-form-item>
        <el-form-item label="供应商(厂家)" size="mini" prop="supplier_id">
          <el-select clearable v-model="params.supplier_id" maxlength="100" :filter-method="supplierListInvok" @focus="supplierListInvok" filterable placeholder="请选择">
            <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品名称" size="mini">
          <v-GoodsList :scopes='{row: params}' :vxeParams='{company_id: params.company_id}' :showObj="{goods_id: '商品编号' , goods_name: '商品名称'}" />
        </el-form-item>
        <el-form-item label="年周批次" size="mini">
          <el-input v-model="params.batch" clearable @keyup.enter.native="onSearch"></el-input>
        </el-form-item>
        <el-form-item label="仓库名称" size="mini" style='margin-left:-60px;'>
          <erpWarehouse v-model="params.warehouse_id" :searchParams='{ company_id: params.company_id }' />
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
          <el-button @click="onReset" size="mini" type="default">重置</el-button>
        </el-form-item>
      </el-form>
      <div class="delet">
        单据类型：采购入库单
      </div>
      <div class="table_container">
        <xTable ref='selectxTable' rowId='selectInstorage' :ajax='{ url: "getInStorageListPublics", params: params }' :maxHeight='300' :tableColumn='searchTableColumn' />
      </div>
      <div style="padding-top: 20px;border-top: 1px dotted #333;margin-top: 20px;">
        <p>单据明细</p>
        <xTable ref='xTable' rowId='instorageList' :xData="instoragInfo" :sign='signObj' :tableColumn='tableColumn' @selectChange='selectChange' />
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { tableHead, paramsModule } from "./config/selectBill";
export default {
  name: "selectCustomLayer",
  mixins: [tableHead],
  data() {
    return {
      authority: {},
      params: this.$publicFution.deepCopy(paramsModule), //弹框表单字段
      signObj: {
        arr: [],
        str: "instorage_id"
      },
      infolist: {},
      instoragInfo: [], //单据明细
      multipleSelection: []
    };
  },
  props: ["selectParams", "editType"],
  methods: {
    instorageDetailFun(row) {
      this.instorage_id = row.instorage_id;
      let vm = this;
      this.$axios({
        method: "get",
        url: this.$api.referenceInstorageInfo,
        params: {
          instorage_id: row.instorage_id
        }
      }).then(({ data }) => {
        if (data.code == 200) {
          var arr = [];
          this.$set(this, "infolist", data.data.info);
          this.$set(this.infolist, "instorage_id", row.instorage_id);
          this.multipleSelection = [];
          data.data.goodsList.forEach(val => {
            if (val.reference_goods_num >= val.goods_num) {
              arr.push(val);
            } else {
              this.multipleSelection.push(val);
            }
          });
          this.signObj = {
            arr: arr,
            str: "instorage_goods_id"
          };
          vm.$set(vm, "instoragInfo", data.data.goodsList);
          setTimeout(() => {
            this.$refs.xTable._proxy("setAllSelection", true);
          }, 50);
        }
      });
    },
    cancel() {
      this.$parent.isShowCustomBill = false;
      this.$parent.readonly = false;
    },
    selectChange(obj) {
      this.multipleSelection = obj.selection;
    },
    submit() {
      if (this.multipleSelection.length == 0) {
        this.$message.warning("请选择需要退货的商品");
        return false;
      }
      let objact = {
        goodsList: this.multipleSelection,
        info: this.infolist
      };
      this.$emit("selectOutstockResult", objact);
      this.$parent.isShowCustomBill = false;
      this.$parent.readonly = false;
    },
    onSearch(number) {
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      this.instoragInfo = [];
      this.$refs.selectxTable._getData(number);
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.timeValue = [];
      this.params = this.$publicFution.filter(paramsModule, this.selectParams);
      this.onSearch(1);
    }
  },
  mounted() {
    this.params = this.$publicFution.filter(paramsModule, this.selectParams);
  }
};
</script>

